                      </v-badge>
                  </v-card>
                  <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                          <v-card-text v-bind="attrs" v-on="on" class="pt-2 font-weight-bold slot-game-name-box text-center elevation-0">
                              {{ getGameName(game.locale) }}
                          </v-card-text>
                      </template>
                      <span>{{ getGameName(game.locale) }}</span>
                  </v-tooltip>
              </v-list-item>
          </template>
      </v-list>
      <p v-if="gameList.length <= 0" class="text-center text-capitalize mb-2">{{ $t(`label.noGameFound`) }}</p>
      <div class="text-center mb-6" v-if="pagination.totalPage > 1">
          <v-pagination class="game_pagination" v-model="pagination.currentPage" :length="pagination.totalPage" @input="changePage" :next-icon="'mdi-chevron-right'" :prev-icon="'mdi-chevron-left'" :total-visible="pagination.paginationPageLimit"></v-pagination>
      </div> -->
    </div>
  </div>
</template>

<script>
import { uiHelper, locale } from '@/util'
import 'pace-js/themes/green/pace-theme-minimal.css'
import 'pace-js'
import { CACHE_KEY, SHARED } from '@/constants/constants'
import { ROUTE_NAME } from '@/constants/route.constants'
import format from 'string-format'
import { GAME_TYPE_GAMES, GAME_CATEGORIES, GAME_PROVIDERS_TYPE_CONFIG } from '@/store/game.module'
import { SEMI_TRANSFER } from '@/store/transfer.module'
import filter from 'lodash/filter'
import findIndex from 'lodash/findIndex'
import WatchTutorialVideo from '@/components/WatchTutorialVideo.vue'

export default {
  name: 'TablePage',
  components: { WatchTutorialVideo },
  props: {
