      let currentGamePage = this.$route.name
      const ae_parms_kv = {
        event_type: 'category_page',
        country_code: 'BD',
        lang_code: uiHelper.getLanguage(),
        category: currentGamePage,
        depth: 1
      }
      if (this.$route.params.provider) {
        this.InsertGameProviderAdelementPixel(ae_parms_kv, 'from $route() watcher')
      } else {
        this.InsertProductAdelementPixel(ae_parms_kv, 'from $route() watcher')
      }
    },
    gameCategory() {
      let d = this.$store.state.game.categories
      this.categories.push({
        id: 0,
        name: 'all',
        icon: 'grid_on'
      })
      d.forEach(category => {
        let categoryName = category.name.toLowerCase()
        this.categories.push({
          id: category.id,
          name: categoryName,
          icon: categoryName == 'hot' ? 'whatshot' : categoryName == 'new' ? 'fiber_new' : ''
        })
      })
    },
