      this.$store.dispatch(`${GAME_PROVIDERS_TYPE_CONFIG}`, { providerTypeConfigObj })
    },
    closePageDialog() {
      this.showDialog = false
    },
    closeIframe() {
      const iframe = document.querySelector('#launchGame_iframe') // Change the selector as needed
      if (iframe) {
        // Slide out the container and remove it after the animation is complete
        const parentContainer = iframe.parentNode
        parentContainer.style.bottom = '-100%'
        setTimeout(() => {
          document.body.removeChild(iframe.parentNode)
        }, 100)
      }
    },
    InsertProductAdelementPixel(ae_parms_kv) {
      console.log('product pixel :', ae_parms_kv)
      window.ae_parms_kv = ae_parms_kv
      const el_adv_id = '71880509ee082f8f44596eedf687aac0'
      const scr = document.createElement('script')
      const host = '//d313lzv9559yp9.cloudfront.net'
      scr.type = 'text/javascript'
      scr.async = true
      scr.src = `${host}/${el_adv_id}.js`
      const s = document.getElementsByTagName('script')[0]
      s.parentNode.insertBefore(scr, s)
    },
    InsertGameProviderAdelementPixel(ae_parms_kv, from) {
      let provider = this.$route.params.provider.toLowerCase()
      let productFullName = locale.getMessage(`ProviderShortToLong.${provider.toUpperCase()}`)
      let category = this.$route.path.replace(provider, '').replaceAll('/', '')

      if (provider == 'sexy_v2') {
        switch (category) {
          case 'tablegames':
            productFullName = locale.getMessage(`ProviderShortToLong.${provider.toUpperCase()}_TABLE`)
            break
          case 'casino':
            productFullName = locale.getMessage(`ProviderShortToLong.${provider.toUpperCase()}_LD`)
            break
        }
      }
      const provider_parms_kv = {
        ...ae_parms_kv,
        category: category,
        event_type: 'product_page',
        product_name: productFullName, // Product name
        product_id: provider, // Unique product identifier
        depth: 2
      }
      console.log('from :', from, ' ,game.vue provider_parms_kv: ', provider_parms_kv)
      window.ae_parms_kv = provider_parms_kv
      const el_adv_id = '71880509ee082f8f44596eedf687aac0'
      const scr = document.createElement('script')
