<script>
import { ROUTE_NAME } from '@/constants/route.constants'
import { locale, uiHelper } from '@/util'
import { SHARED } from '@/constants/constants'
export default {
  name: 'errorPageNotFound',
  data: () => ({
    routeName: ROUTE_NAME
  }),
  mounted() {
    uiHelper.setMetaTag('robots', 'noindex')
  },
  beforeRouteLeave(to, from, next) {
    if (SHARED.INDEXED === 'false') {
      uiHelper.setMetaTag('robots', 'noindex, follow')
    } else {
      uiHelper.setMetaTag('robots', 'index, follow, noarchive, all')
    }

    next()
  },
  methods: {
    // openLiveChat() {
    //   window.open('/livechat', 'Live Chat', 'width=350,height=600')
    // },
    goHome() {
      this.$router.push({
        name: ROUTE_NAME.HOME
      })
    }
  }
}
